import React from "react";
const Formulario = () => {
    return (
        <form className="login">
            <h1 className="login-title"> Bienvenido </h1>
            <h5 className="login-title">Comienza.¡Es Gratis!</h5>
            Nombre
            <br />
            <input type="text" className="login-input" autofocus />
            <br />
            Apellido
            <br />
            <input type="text" className="login-input" />
            <br />
            Correo Electronico
            <input type="text" className="login-input" placeholder="Email Adress" />
            Contraseña(6 o mas caracteres)
            <input type="password" className="login-input" placeholder="Password" />
            <br />
            <h6>Al hacer clic en unirte aceptas las Condiciones de uso la Politica de privacidad y la Politica de cookies de esta pagina</h6>
            <br />

            <input type="submit" value="Únete ahora" className="login-button" />

        </form>
    )

}
export default Formulario