import React from "react";
import { Outlet } from "react-router-dom";
import { Link } from "react-router-dom";
const Navbar = () => {
    return (
        <div>
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                <div className="container-fluid">
                    <a className="navbar-brand" >Menu</a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <Link className="nav-link active" aria-current="page" to="/" >Inicio</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="/crear" >Crear Cuenta</Link>
                            </li>
                            <li className="nav-item dropdown">
                                <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                    Lenguajes de Programacion
                                </a>
                                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                                <li><Link className="dropdown-item" to="/carr">Carrusel</Link></li>
                                    <li><Link className="dropdown-item" to="/pag">Java</Link></li>
                                    <li><Link className="dropdown-item" to="/pag1">JavaScript </Link></li>
                                    <li><Link className="dropdown-item" to="/pag2">ReactJS </Link></li>
                                    <li><Link className="dropdown-item" to="/pag3">Phyton </Link></li>
                                    <li><Link className="dropdown-item" to="/pag4">C# </Link></li>
                                    <li><hr className="dropdown-divider"/></li>
                                    <li><a className="dropdown-item" href="#">Something else here</a></li>
                                </ul>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="/pag5" >Creditos</Link>
                            </li>
                            
                        </ul>
                        <a className="nav-link disabled" href="#" tabindex="-1" aria-disabled="true">Inicie Sesion</a>
                        <form className="d-flex">
                            <input className="form-control me-2" type="search" placeholder="Correo Electronico" aria-label="Search"/>
                            <input className="form-control me-2" type="search" placeholder="Contraseña" aria-label="Search"/>
                                <button className="btn btn-outline-success btn-lg" type="submit">Empezar</button>
                        </form>
                    </div>
                </div>
            </nav>
            <Outlet/>
        </div>
    )
}
export default Navbar